import * as action_types from "../store/action_types";
import { getFriendAnswersFromKey } from "../network";

export const saveAuthorAnswers = (payload) => {
  return {
    type: action_types.SAVE_AUTHOR_ANSWERS,
    payload,
  };
};

export const saveAuthorKey = (payload) => {
  return {
    type: action_types.SAVE_AUTHOR_KEY,
    payload,
  };
};

export const saveFriendKey = (payload) => {
  return {
    type: action_types.SAVE_FRIEND_KEY,
    payload,
  };
};

export const saveFriendName = (payload) => {
  return {
    type: action_types.SAVE_FRIEND_NAME,
    payload,
  };
};

export const saveAuthorName = (payload) => {
  return {
    type: action_types.SAVE_AUTHOR_NAME,
    payload,
  };
};

export const saveAuthorGender = (payload) => {
    console.log("saveAuthorGender", payload);
    return {
      type: action_types.SAVE_AUTHOR_GENDER,
      payload,
    };
  };

export const saveQuestions = (payload) => {
  return {
    type: action_types.SAVE_QUESTIONS,
    payload,
  };
};

export const saveFriendAnswers = (payload = "") => async (dispatch, getState) =>
  getFriendAnswersFromKey(payload).then(function (response) {
    console.log(response.data);
    if (response.data !== null) {
      const { answers, name } = response.data.authorData;

      dispatch({
        type: action_types.IS_FRIEND,
        payload: true,
      });
      dispatch({
        type: action_types.SAVE_FRIEND_ANSWERS,
        payload: answers,
      });
      dispatch(saveFriendName(name));
    } else {
      dispatch(showLoader(false));
      alert("Cannot login. Please check URL or Internet connection");
    }
    return getState().isFriend;
  });

export const showLoader = (payload = false) => {
  return {
    type: action_types.SHOW_LOADER,
    payload,
  };
};
