import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button from '../../elements/button';
import fbConfig from '../../config/firebase_config';
import { getAllFriendsAnswers } from '../../network';
import './result.css';

class Result extends Component {


    state = {
        friendScoreList: [],
        friendData: {
            name: "",
            answers: {},
            score: {
                answered: "",
                totalQuestion: ""
            }
        }
    }
    showResultRanOnce = false;

    componentDidMount() {
        const { friendKey, authorName, authorAnswers, friendAnswers, questions } = this.props;
        let totalQuestion = questions.length;
        console.log(friendAnswers);
        let answered = 0;
        let friendAns = null;
        let _this = this;

        for (let key in authorAnswers) {
            if (authorAnswers[key] === friendAnswers[key]) answered += 1;
        }

        this.setState({
            friendData: {
                name: authorName,
                answers: authorAnswers,
                score: {
                    answered,
                    totalQuestion
                }
            }
        }, () => {
            if(friendKey){
                friendAns = fbConfig.database().ref(`/${friendKey}/friendsAnswers`).push({ name: _this.state.friendData.name, answered: _this.state.friendData.score.answered, totalQuestions: _this.state.friendData.score.totalQuestion });
                friendAns.then(() => {
                    this.getFriendsScoreList();
                })
            }
        })
    }
    showShareLink(shareLink) {
        function copyToClipboard(text) {
            var dummy = document.createElement("textarea");
            // to avoid breaking orgain page when copying more words
            // cant copy when adding below this code
            // dummy.style.display = 'none'
            document.body.appendChild(dummy);
            //Be careful if you use texarea. setAttribute('value', value), which works with "input" does not work with "textarea". – Eduard
            dummy.value = text;
            dummy.select();
            document.execCommand("copy");
            document.body.removeChild(dummy);
        }

        let sL = `${window.location.origin}/quiz/${shareLink}`;

        return <div className="share-link-container">
            <p className="msg-thanks">Thanks for playing! </p>
            <p className="msg-info">Share the link below with your friends and find out how well they know you.</p>
            <p className="share-link-code" disabled={true}>{sL}</p>
            <a className="button is-primary is-large" color="is-link is-light is-large" href={`href="whatsapp://send?text=${sL}"`} data-action="share/whatsapp/share">Share</a>
            <Button color="is-link is-light is-large" onClick={() => copyToClipboard(sL)}>Copy</Button>

        </div>

    }

    getFriendsScoreList() {
        console.log("showFriendsScoreList");
        const { friendKey } = this.props;
        let _this = this;
        getAllFriendsAnswers(friendKey).then(function (friendsScores) {
            const allFriendScore = [];
            const allScores = friendsScores.data;
            console.log("fd", allScores);
            for (let key in allScores) {
                allFriendScore.push({ key, name: allScores[key].name, answered: allScores[key].answered, totalQuestions: allScores[key].totalQuestions })
            }
            console.log("allFriendScore", allFriendScore);
            _this.setState({
                friendScoreList: [...allFriendScore]
            })
        });

    }
    showFriendsScoreList(friendScoreList) {
        return this.state.friendScoreList ? (<table className="table">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Score</th>
                </tr>
            </thead>
            <tbody>
                {friendScoreList.map(item => <tr key={item.key}>
                    <td>{item.name}</td>
                    <td>{item.answered}</td>
                </tr>)}
            </tbody>
        </table>) : '';
    }
    render() {
        console.log("rendering again", this.showResultRanOnce);
        const { authorKey, friendKey, authorName, authorAnswers, friendAnswers, questions, isFriend } = this.props;
        const { score: { answered, totalQuestion } } = this.state.friendData;
        return (
            <div className="cmp-result">
                <div>
                    {/* {isFriend ? this.showResult(authorName, authorAnswers, friendAnswers, questions, friendKey) : this.showShareLink(authorKey)} */}
                    {isFriend ? (<div className="score-container v-center">
                        <h4 className="score-title">Your score</h4>
                        <p className="score-data">
                            {answered}/{totalQuestion}
                        </p>
                    </div>) : this.showShareLink(authorKey)}
                </div>
                <div>
                    {isFriend ? this.showFriendsScoreList(this.state.friendScoreList) : ""}
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    const { authorName, authorKey, friendKey, authorAnswers, friendAnswers, questions, isFriend } = state;
    return {
        authorName,
        authorKey,
        friendKey,
        authorAnswers,
        friendAnswers,
        questions,
        isFriend
    }
}

export default connect(mapStateToProps)(Result);