import React from 'react';
import './radio.css';


const Radio = ({image, value, onRadioChange, imageOpacity}) => {
    console.log(image);
    return <label className="radio">   
         <input type="radio" name="gender" value={value} onChange={onRadioChange}/>
         <img width="50" style={{opacity: imageOpacity}} src={image} alt=""/>     
    </label>
}

export default Radio;
