import * as action_types from './action_types'

export const authorAnswersReducer = (authorAnswers = {}, action) => {
    if (action.type === action_types.SAVE_AUTHOR_ANSWERS) {
        console.log("action.payload", action.payload);
        return { ...action.payload }
    }
    return authorAnswers;
}


export const friendAnswersReducer = (friendAnswers = {}, action) => {
    if (action.type === action_types.SAVE_FRIEND_ANSWERS) {
        console.log("action.payload", action.payload);
        return { ...action.payload }
    }
    return friendAnswers;
}

export const isFriendReducer = (isFriend = false, action) => {
    if (action.type === action_types.IS_FRIEND) {
        console.log("action.payload", action.payload);
        return action.payload
    }
    return isFriend;
}

export const showLoaderReducer = (loader = false, action) => {
    if (action.type === action_types.SHOW_LOADER) {
        return action.payload
    }
    return loader;
}

export const authorKeyReducer = (authorKey = "", action) => {
    if (action.type === action_types.SAVE_AUTHOR_KEY) {
        return action.payload
    }
    return authorKey
}

export const friendKeyReducer = (friendKey = "", action) => {
    if (action.type === action_types.SAVE_FRIEND_KEY) {
        return action.payload
    }
    return friendKey
}

export const authorNameReducer = (authorName = "", action) => {
    if (action.type === action_types.SAVE_AUTHOR_NAME) {
        console.log("authorNameReducer",authorName);
        return action.payload
    }
    return authorName
}

export const authorGenderReducer = (authorGender = "", action) => {
    console.log("authorGenderReducer",action);
    if (action.type === action_types.SAVE_AUTHOR_GENDER) {
        return action.payload
    }
    return authorGender
}


export const friendNameReducer = (friendName = "", action) => {
    if (action.type === action_types.SAVE_FRIEND_NAME) {
        return action.payload
    }
    return friendName
}

export const questionsReducer = (questions = [], action) => {
    if (action.type === action_types.SAVE_QUESTIONS) {
        return action.payload
    }
    return questions
}