import axios from 'axios';
import {BASE_URL} from './api_constants';

export const getFriendAnswersFromKey = (key) => {
    return axios({
        method: 'get',
        baseURL:BASE_URL,
        url: `${key}.json`,
        responseType: 'stream'
    })
}


export const getAllFriendsAnswers = (key) => {
    return axios({
        method: 'get',
        baseURL:BASE_URL,
        url: `${key}/friendsAnswers.json`,
        responseType: 'stream'
    })
}

