import { combineReducers, createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import * as reducers from './reducers';

const combinedReducers = combineReducers({
    loader: reducers.showLoaderReducer,
    isFriend: reducers.isFriendReducer,
    authorKey: reducers.authorKeyReducer,
    friendKey: reducers.friendKeyReducer,
    friendAnswers: reducers.friendAnswersReducer,
    authorAnswers: reducers.authorAnswersReducer,
    friendName: reducers.friendNameReducer,
    authorName: reducers.authorNameReducer,
    authorGender: reducers.authorGenderReducer,
    questions: reducers.questionsReducer
});

const composed = compose(applyMiddleware(thunk),
window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())

export default createStore(combinedReducers, composed);