import React, { Component } from 'react';
import { connect } from 'react-redux';
import { saveFriendAnswers, showLoader, saveFriendKey, saveAuthorName, saveAuthorGender } from '../../store/actions_creator';
import Button from '../../elements/button';
import Input from '../../elements/Input';
import Radio from '../../elements/Radio';
import Girl from '../../assets/girl.png';
import Boy from '../../assets/boy.png';
import "./welcome.css";

class Welcome extends Component {
    state = {
        inputText: '',
        gender: ''
    }

    componentDidMount() {
        const { pathname } = this.props.location;
        console.log("welcome didMount", this.props);
    }
    handleOnclick = () => {
        const { match: { params, path }, saveFriendAnswers, history, showLoader, saveFriendKey, saveAuthorName,saveAuthorGender } = this.props;
        showLoader(true);
        console.log(this.state.inputText);
        saveAuthorName(this.state.inputText);
        saveAuthorGender(this.state.gender);
        if (params.id && path === "/quiz/:id") {
            saveFriendAnswers(params.id).then((isFriend) => {
                if (isFriend) {
                    saveFriendKey(params.id);
                    history.push("/question");
                    showLoader(false);
                }
            });
        } else {
            if (path === "/") {
                history.push("/question");
                showLoader(false);
            }
        }

    }
    handleInputTextChange = (e) => {
        console.log(e.target.value);
        e.persist();
        this.setState((previousState) => ({
            ...previousState,
            inputText: e.target.value
        }))
    }

    handleRadioSelectChange = (e) => {
        console.log(e.target.value);
        e.persist();
        this.setState((previousState) => ({
            ...previousState,
            gender: e.target.value
        }))
    }
    render() {
        console.log("gender is", this.state.gender);
        return (
            <div className="welcome-cmp">
                <h2 className="game-title">Friendship Challenge</h2>
                <div className="container v-center">
                    <div className="box v-center">
                        <div className="field">
                            <div className="control">
                                <Input value={this.state.inputText} onInputChange={this.handleInputTextChange} />
                                <div className="gender">
                                    <Radio value="f" imageOpacity="0.8" image={Girl} onRadioChange={this.handleRadioSelectChange} />
                                    <Radio value="m" imageOpacity="1" image={Boy} onRadioChange={this.handleRadioSelectChange} />
                                </div>
                            </div>
                            <div className="btn-add">
                                <Button disabled={this.state.inputText.length && this.state.gender.length ? false : true} size="is-big" color="is-primary" onClick={this.handleOnclick}>Lets Start</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = ({ isFriend }) => ({
    isFriend
})

export default connect(mapStateToProps, { saveFriendAnswers, showLoader, saveFriendKey, saveAuthorName, saveAuthorGender })(Welcome)