import React, { Component } from "react";
import { connect } from "react-redux";
import questions from "../../data";
import Button from "../../elements/button";
import Option from "../Option";
import {
  saveAuthorAnswers,
  saveAuthorKey,
  saveQuestions,
} from "../../store/actions_creator";
import {changeSentenceBasedOnGender} from "../../helper";
import fbConfig from "../../config/firebase_config";
import "./question.css";

class Question extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentQuestion: {
        answers: {},
        id: null,
        question: "",
        options: [],
      },
      selectedOptionIndex: null,
      authorAnswers: {},
    };
    this.counter = 0;
    this.questions = [];
  }
  componentDidMount() {
    const { saveQuestions, isFriend, friendName } = this.props;
    let modifiedQuestions = questions.map(item => {
        return {
            ...item,
            friendQuestion: changeSentenceBasedOnGender(item.friendQuestion, friendName, "m")
        }
    });
    
    saveQuestions([...questions]);
    this.questions = [...modifiedQuestions];

    console.log("questions", this.questions);
    this.setState({
      currentQuestion: {
        question: isFriend
          ? this.questions[this.counter].friendQuestion
          : this.questions[this.counter].question,
        id: this.questions[this.counter].id,
        options: [...this.questions[this.counter].options],
      },
    });
  }
  handleNextClick = () => {
    const { isFriend } = this.props;
    this.counter = this.counter + 1;
    this.setState({
      selectedOptionIndex: null,
    });
    if (this.counter < this.questions.length) {
      this.setState({
        currentQuestion: {
          question: isFriend
            ? this.questions[this.counter].friendQuestion
            : this.questions[this.counter].question,
          id: this.questions[this.counter].id,
          options: [...this.questions[this.counter].options],
        },
      });
    } else {
      const {
        saveAuthorAnswers,
        history,
        saveAuthorKey,
        isFriend,
        authorKey,
        authorName,
        friendKey,
        friendAnswers,
      } = this.props;
      saveAuthorAnswers(this.state.authorAnswers);
      if (!isFriend) {
        const authorData = {
          name: authorName,
          answers: this.state.authorAnswers,
        };
        let ref = fbConfig.database().ref(`/${authorKey}`).push({ authorData });
        console.log(ref);
        ref.on("value", function (data) {
          console.log("res data", data);
          saveAuthorKey(data.key);
        });
      }
      history.push("/result");
      this.counter = 0; // Resetting counter back to 0
    }
  };

  // handleBackClick = () => {
  //     if ((this.counter < this.questions.length) && (this.counter > 0)) {
  //         this.counter = this.counter - 1;
  //         this.setState({
  //             currentQuestion: {
  //                 question: this.questions[this.counter].question,
  //                 id: this.questions[this.counter].id,
  //                 options: [...this.questions[this.counter].options]
  //             }
  //         });
  //     }
  // }

  handleAnswerClick = (questionId, selectedAns, selectedOptionIndex) => {
    this.setState({
      selectedOptionIndex,
      authorAnswers: { ...this.state.authorAnswers, [questionId]: selectedAns },
    });

    setTimeout(()=> {
      this.handleNextClick();
    },1000)
  };

  render() {
    console.log(this.counter);
    const { selectedOptionIndex } = this.state;
    // ${isSelected ? 'selected-option' : ''}
    return (
      <div className="cmp-question">
        <p className="question">{this.state.currentQuestion.question}</p>
        <div className="q-option-container is-flex">
          {this.state.currentQuestion.options.map((item, index) => {
            return (
              <div
                key={index}
                className={`q-option ${
                  selectedOptionIndex === index ? "selected-option" : ""
                }`}
              >
                <Option
                  onClick={(e) =>
                    this.handleAnswerClick(
                      this.state.currentQuestion.id,
                      item,
                      index
                    )
                  }
                >
                  {item}
                </Option>
              </div>
            );
          })}
        </div>
        {/* <div className="controls">
          <Button
            disabled={selectedOptionIndex !== null ? false : true}
            color="is-primary is-light"
            size="is-large"
            onClick={this.handleNextClick}
          >
            {this.counter === this.questions.length - 1 ? "Submit" : "Next"}
          </Button>
        </div> */}
      </div>
    );
  }
}
const mapStateToProps = ({
  isFriend,
  authorKey,
  authorName,
  friendName,
  friendKey,
  friendAnswers,
}) => ({
  isFriend,
  authorName,
  friendName,
  authorKey,
  friendKey,
  friendAnswers,
});
export default connect(mapStateToProps, {
  saveAuthorAnswers,
  saveAuthorKey,
  saveQuestions,
})(Question);
